@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@200&display=swap);
* {
  font-family: "Kanit", sans-serif;
  font-weight: 600;
}

* {
  background-color: #000;
  color: #f4f4f4;
  font-family: "Kanit", sans-serif;
}

.app {
  min-height: 100vh;
  max-width: 100vw;
  padding: 20px;
}

@media (max-width: 992px) {
  .app {
    max-width: 95vw;
    padding: 10px;
    overflow: hidden;
  }

  .content-container {
    width: 100%;
    display: inline-flex;
    justify-content: space-between;
  }
}

.header {
  display: flex;
  height: 100px;
  justify-content: space-between;
  align-items: center;
}

.header > div {
  margin: 10px;
}

.img-logo {
  width: 200px;
}

.search-container {
  flex: 1 1;
  background-color: #1c1c1c;
  height: 50px;
  border-radius: 50px;
  display: flex;
}

.search-icon {
  margin: 10px;
  background-color: transparent;
}

.search-bar {
  background-color: transparent;
  border: none;
  outline: none;
  width: 100%;
  font-size: 16px;
  color: #eee;
}

.search-bar::-webkit-input-placeholder {
  font-weight: 600;
}

.search-bar:-ms-input-placeholder {
  font-weight: 600;
}

.search-bar::placeholder {
  font-weight: 600;
}

.headerItems > button {
  justify-content: space-between;
  border: none;
  background-color: transparent;
  font-size: 16px;
  cursor: pointer;
  margin: 10px;
}

.headerActions > button {
  justify-content: space-between;
  margin: 10px;
  cursor: pointer;
  font-size: 16px;
}

.themeSwitch {
  border-radius: 50%;
  border: none;
  background-color: #1c1c1c;
  padding: 15px;
  height: 50px;
  object-fit: contain;
}

.themeSwitch > img {
  background-color: transparent;
  height: 20px;
}

.loginButton {
  background: linear-gradient(to right, #59f9b7, #66feaa);
  border-radius: 25px;
  color: #1c1c1c;
  padding: 15px 40px;
}

@media (max-width: 992px) {
  .img-logo {
    width: 125px;
  }

  .headerItems {
    display: none;
  }

  .headerActions {
    display: none;
  }
}

.showCardDetails {
  display: flex;
  width: 95vw;
  max-height: 50vh;
  border-bottom: 1px solid #a1a5be;
  margin-top: 30px;
}

.punkImage {
  width: 200px;
  height: 200px;
  border-radius: 20px;
  margin: 20px 20px 5px;
}

.details {
  margin-left: 50px;
}

.upperSection {
  align-items: center;
  margin-bottom: 40px;
}

.heading {
  display: flex;
}

.heading > h2 {
  font-size: 65px;
  font-weight: 900;
  margin-top: 0;
}

.heading > p {
  color: #a1a5be;
  font-size: 40px;
  font-weight: 700;
  margin-top: 0;
  margin-left: 10px;
}

.punkDescription {
  font-size: 25px;
  font-family: monospace;
  color: #a1a5be;
  margin-top: 0;
}

.lowerSection {
  display: flex;
  justify-content: space-between;
}

.ownerInfo {
  flex: 1 1;
  width: 50vw;
  margin: 0;
  margin-left: 20px;
}

.ownerInfo > h3 {
  color: steelblue;
  margin-top: 0;
}

.ownerInfo > p {
  margin-bottom: 2px;
}

.ownerImage {
  margin: 0;
  width: 75px;
  height: 75px;
  border-radius: 50%;
}

.socialMedia {
  display: flex;
  margin: 0;
}

.socialMedia > img {
  width: 40px;
  height: 40px;
  border: 1px solid #fff;
  margin: 10px;
  border-radius: 50%;
  padding: 10px;
}

@media (max-width: 992px) {
  .showCardDetails {
    display: flex;
    flex-direction: column;
    width: 250px;
    border-bottom: none;
  }

  .punkImage {
    height: 150px;
    width: 150px;
  }

  .details {
    margin-left: 25px;
  }

  .heading > h2 {
    font-size: 25px;
  }

  .heading > p {
    font-size: 20px;
  }

  .ownerImage {
    height: 35px;
    width: 35px;
  }

  .ownerInfo {
    padding: 0;
    flex-direction: column;
    width: 50vw;
    margin-left: 5px;
  }

  .ownerInfo > p {
    display: none;
  }

  .socialMedia {
    display: none;
  }
}

.collectionCard {
  background-color: #1c1c1c;
  border-radius: 20px;
  overflow: hidden;
  width: 300px;
  height: 550px;
  margin-right: 30px;
}

.cardDetails {
  background-color: transparent;
  padding: 10px;
}

.punkName {
  background-color: transparent;
  font-size: 25px;
  font-weight: bolder;
}

.nftId {
  background-color: transparent;
  color: #a1a5be;
  font-size: 15px;
  font-weight: 600;
}

.description {
  background-color: transparent;
  color: #a1a5be;
  font-size: 15px;
  font-weight: 600;
}

.collectionCardImage {
  width: 100%;
}

.cardPriceContainer {
  background-color: transparent;
  display: flex;
  align-items: center;
  margin-top: 20px;
  object-fit: contain;
}

.eth-img {
  background-color: transparent;
  width: 15px;
  height: 25px;
}

.cardPrice {
  background-color: transparent;
  font-weight: 700;
  margin-left: 10px;
}

@media (max-width: 992px) {
  .collectionCard {
    margin: 10px;
    height: 100px;
    width: 100px;
  }
}

.punkListContainer {
  display: flex;
  cursor: pointer;
  overflow-x: scroll;
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #a1a5be;
  scroll-behavior: smooth;
}

.punkListContainer::-webkit-scrollbar {
  display: none;
}

@media (max-width: 992px) {
  .punkListContainer {
    flex-direction: column;
    border-bottom: none;
    overflow-y: scroll;
  }

  .punkListContainer::before {
    width: 100px;
    height: 100px;
    background-color: #a1a5be;
  }
}

